@import "../core/variables";
@import "./links.scss";

.section__title {
  display: flex;
  justify-content: space-between;
  font-weight: 600;
  font-size: 18px;
  text-transform: uppercase;
  color: $gunmetal;
  padding: 10px;
  border-bottom: 1px solid #d8d8d8;
  .arrow {
    flex: 2;
    text-align: end;
  }
  .title {
    padding: 0 10px;
  }
}
.div_content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 7px;
  padding: 30px 0;
}
.label {
  padding-top: 13px;
  font-size: 14px;
  color: $darkgreenblue;
  grid-column: span 2;
  .span {
    color: $dullorange;
  }
}
.action__upload-btn {
  width: 140px;
  height: 30px;
  font-size: 14px;
  font-family: $mainfont;
  color: #ffffff;
  background-color: $dullorange;
  border: none;
  border-radius: 3px;
  white-space: nowrap;
  padding-left: 22.24px;
  padding-right: 22.23px;
  padding-top: 6px;
  padding-bottom: 5px;
}
.action__hiddenField {
  display: none;
}
.profile__image,
.profile__preview {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid #ccc;
}
.profile__preview {
  width: 30px;
  height: 30px;
  border: solid 1px $hospitalgreen;
  grid-column: 2 / 3;
  grid-row: span 2;
}
